/**/
footer.fixed {
  position: absolute;
  bottom: 0;
  left: 0; }

footer {
  width: 100%;
  z-index: 99999999;
  color: #f6f6f6;
  background-image: url(../img/patterns/0.png);
  background-color: #24ceff;
  font-size: 1.4rem;
  line-height: 100%;
  font-weight: 400; }
  footer .link {
    color: #f6f6f6;
    margin: 20px 0;
    text-align: center;
    display: block;
    font-size: 1.8rem;
    line-height: 100%;
    font-weight: 100; }
    footer .link:hover {
      text-decoration: underline; }
  footer .branding img {
    display: inline-block;
    vertical-align: top;
    white-space: normal;
    margin: 0 20px;
    height: 40px; }
  footer .social {
    text-align: center;
    margin: 20px 0; }
    footer .social a {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
      text-align: center;
      height: 40px;
      width: 40px;
      border-radius: 100%;
      margin: 0 3px;
      background: rgba(255, 255, 255, 0.5);
      transition: all 600ms; }
      footer .social a:hover {
        background: white; }
        footer .social a:hover i {
          color: #1AC4FF; }
      footer .social a i {
        margin-top: 8px;
        color: #1AC4FF;
        font-size: 2.6rem;
        line-height: 100%;
        font-weight: normal;
        transition: all 0.2s; }
  footer .text {
    color: #f6f6f6;
    font-size: 1.6rem;
    line-height: 150%;
    font-weight: 300;
    margin-bottom: 20px; }
  footer .capa {
    background: #1AC4FF;
    padding: 10px 0; }
    footer .capa .row {
      margin-bottom: 0; }
  footer a {
    color: #1AC4FF; }
  footer .row {
    margin-bottom: 0; }
  footer figure {
    margin: 30px 0;
    width: 100%;
    text-align: center; }
    footer figure img {
      height: 50px;
      opacity: 0.6; }
  footer nav {
    margin: 25px 0 20px;
    border-left: thin dotted #A6A8AB; }
    footer nav ul {
      width: 100%;
      text-align: center; }
      footer nav ul li {
        display: block;
        margin: 0 0px; }
        footer nav ul li a {
          padding: 10px 0px 10px 20px;
          text-align: left;
          text-transform: uppercase;
          display: block;
          transition: all 0.4s;
          color: white; }
          footer nav ul li a:hover {
            color: #f6f6f6; }
      footer nav ul .last {
        border: none !important; }
  footer .contact-info {
    width: 100%;
    margin: 10px 0 20px;
    text-align: center; }
    footer .contact-info > div {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
      margin: 0 5px; }
      footer .contact-info > div i {
        margin-right: 5px; }
      footer .contact-info > div a {
        color: white;
        text-decoration: underline;
        transition: all 0.4s; }
        footer .contact-info > div a:hover {
          color: #f6f6f6; }
  footer .copyright {
    margin-top: 6px;
    display: block;
    color: white; }
  footer .ahorranito {
    text-align: right;
    margin-top: 3px; }
    footer .ahorranito a {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
      color: white;
      vertical-align: middle; }
    footer .ahorranito .ahorranito-icon {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
      vertical-align: middle; }

@media screen and (max-width: 960px) {
  footer .ahorranito, footer .copyright {
    margin-bottom: 10px;
    text-align: center;
    line-height: 120%; } }

@media screen and (max-width: 601px) {
  footer nav ul li a, footer .contact-info > div {
    line-height: 150%; } }

.ahorranito span {
  color: #1d9ad4; }
.ahorranito .ahorranito-icon {
  background-image: url(../img/ahorranito.png);
  background-position: center;
  background-repeat: no-repeat;
  display: inline-block;
  height: 20px;
  vertical-align: middle;
  width: 20px; }

footer {
  position: relative;
  overflow: hidden; }
  footer .icon2 {
    position: absolute;
    bottom: -40px;
    right: -50px;
    height: 320px;
    width: 240px;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    opacity: 0.1; }

