/**/
*, *:before, *:after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.btn, .btnSec, .btnTer, .btn-large {
  background: #1AC4FF;
  color: #f6f6f6;
  border-radius: 4px !important; }
  .btn:hover, .btnSec:hover, .btnTer:hover, .btn-large:hover {
    background: #2ed8ff;
    color: #f6f6f6; }

.btnSec {
  background: #4F6CC1;
  color: #f6f6f6; }
  .btnSec:hover {
    background: #6380d5;
    color: #f6f6f6; }
  .btnSec:focus {
    background: #7794e9;
    color: #f6f6f6; }

.btnTer {
  background: transparent;
  border: 2px solid #4F6CC1 !important;
  color: #f6f6f6;
  position: relative; }
  .btnTer:hover {
    background: transparent;
    border: 2px solid #6380d5 !important;
    color: #f6f6f6; }
    .btnTer:hover::after {
      height: 110%;
      width: 110%; }
  .btnTer:focus {
    background: transparent;
    border: 2px solid #7794e9 !important;
    color: #f6f6f6; }
  .btnTer::after {
    content: '';
    display: block;
    top: 0;
    left: 0;
    position: absolute;
    transition: all 0.4s;
    z-index: -1;
    height: 100%;
    width: 0;
    background: #6380d5; }

select, input[type="text"], input[type="password"], textarea {
